@import url('https://fonts.googleapis.com/css?family=Raleway&display=swap');

.FormContainer{
    margin: 0;
    padding: 0;
    display: flex;
    flex-flow: row;
    align-items: center;
    justify-content: center;
    padding: 50px 0;
}

.Input{
    width: 1000px;
    height: 30px;
    outline: none;
    font-family: 'Raleway', sans-serif;
    font-size: 25px;
}

.Button{
    height: 36px;
    width: 100px;
    font-family: 'Raleway', sans-serif;
    font-size: 15px;
}